import { t } from 'sn-translate';
import truncate from 'lodash/truncate';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-pill';
import '@servicenow/now-heading';
import { SMALL, MEDIUM, HEADER_TERTIARY, HEADER_SECONDARY } from './constants';

const PillList = (props) => {
	const { title, pills, behaviors, selectedPillId, componentSize } = props;
	const pillSize = componentSize === SMALL ? SMALL : MEDIUM;
	const headerSize = componentSize === SMALL ? HEADER_TERTIARY : HEADER_SECONDARY;

	return (
		<div className="now-pill-list-root-element">
			{title ? <now-heading label={t(title)} level="1" variant={headerSize}
				className="now-pill-list-header"></now-heading> : ''}
			{pills.map(function (content, pillId) {
				let pillLabel = content.pillText;
				if (typeof pillLabel === 'string' && pillLabel.trim() !== '') {
					return <now-pill label={t(pillLabel.trim())} size={pillSize} className={'now-pill-element ' + pillSize} selected={selectedPillId === pillId} append-to-payload={{pillId, pillValue: content.pillValue} }></now-pill>;
				} else {
					return;
				}
			})
			}
		</div>
	);
};

export default (state) => {
	const { title, pills } = state.properties;
	let { maxPillLimit } = state.properties;
	const { behaviors, selectedPillId, componentSize } = state;
	const props = {
		title: title ? truncate(title.trim(), { length: 120 }) : '',
		pills: pills.slice(0, maxPillLimit),
		behaviors,
		selectedPillId, 
		componentSize
	};

	if (!isEmpty(props.pills) && maxPillLimit > 0) {
		return (<PillList {...props} />);
	}
};