import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import mediaQueryBehavior from '@servicenow/behavior-media-query';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view.js';
import styles from './styles.scss';
import { NOW_PILL_SELECTED_SET, TOGGLE_BREAKPOINT, NOW_PILL_CLICKED, MEDIUM, SMALL } from './constants';
import get from 'lodash/get';

/**
* List of pill components that represent the metadata associated with an object or objects, such as an article, record, or person. 
*
*
* @summary Displays a collection of pills or tags. When a user selects a pill, a page related to that pill or tag opens.
* @seismicElement now-pill-list
*/
createCustomElement('now-pill-list', {
	renderer: { type: snabbdom },
	properties: {
		/**
		* Specifies the title for the pills.
		* @type {string}
		*/
		title: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Array of pills to display, along with their URLs. Required.
		* @type {Pill[]}
		*/
		pills: {
			default: [],
			schema: {
				type: 'array'
			}
		},
		/**
		* Defines the maximum number of pills to display.
		* Default value: 10.
		* @type {number}
		*/
		maxPillLimit: {
			default: 10,
			schema: {
				type: 'number'
			}
		}
	},
	view,
	styles,
	initialState: {
		componentSize: MEDIUM
	 },
	actionHandlers: {
		[actionTypes.COMPONENT_RENDERED]: ({ host, updateState }) => {
			const width = get(host, 'shadowRoot.lastChild.offsetWidth');
			let componentSize = MEDIUM;
			
			if (width <= TOGGLE_BREAKPOINT) {
				componentSize = SMALL;
			}
			updateState({componentSize});
		},

		[NOW_PILL_SELECTED_SET]: ({ action, updateState, dispatch }) => {
			const { pillId } = action.payload;
			updateState({
				path: 'selectedPillId',
				value: pillId,
				operation: 'set'
			});
			dispatch(NOW_PILL_CLICKED, action.payload);
		}
	},
	dispatches: {
		/**
		* @private
		*/
		'NOW_PILL_CLICKED': {}
	},
	behaviors: [
		{
			behavior: rtlBehavior,
		}
	]
});
